@charset "UTF-8";
/* ------------------------------------ *\
  CALLOUT
\* ------------------------------------ */
.fr-callout {
  position: relative;
  padding: 1.5rem;
  --title-spacing: 0 0 0.5rem;
  --text-spacing: 0;
  /**
   * Inclusion de l'icône (optionnel)
   */
  /**
   * Ajout de marge quand on inclut un bouton (optionnel)
   */
}

@media (min-width: 48em) {
  .fr-callout {
    padding: 2rem 3rem;
  }
}

.fr-callout[class^="fr-fi-"]::before, .fr-callout[class*=" fr-fi-"]::before {
  display: block;
  margin: -0.5rem 0 0.5rem;
}

@media (min-width: 48em) {
  .fr-callout[class^="fr-fi-"]::before, .fr-callout[class*=" fr-fi-"]::before {
    margin: -1rem 0 1rem -2rem;
  }
}

.fr-callout__title {
  font-weight:  700 ;
  font-size:  1.375rem ;
  line-height:  1.75rem ;
}

@media (min-width: 48em) {
  .fr-callout__title {
    font-size:  1.5rem ;
    line-height:  2rem ;
  }
}

.fr-callout__text {
  font-size:  1.125rem ;
  line-height:  1.75rem ;
}

@media (min-width: 48em) {
  .fr-callout__text {
    font-size:  1.25rem ;
    line-height:  2rem ;
  }
}

.fr-callout .fr-btn {
  margin-top: 1rem;
}

.fr-callout {
  --blend: var(--background-contrast-grey-blend);
  background-color: var(--background-contrast-grey);
  box-shadow: inset 0.25rem 0 0 0 var(--border-default-blue-france);
}

.fr-callout::before {
  color: var(--text-title-grey);
}

.fr-callout__title {
  color: var(--text-title-grey);
}

